import React, { Component } from "react";
import SearchBar from "../../components/SearchBar";
import { Link } from "react-router-dom";

import "./styles.css";

export default class Main extends Component {
  constructor(props) {
    super(props);

    this.state = {
      rover: "curiosity",
      sol: 1,
      camera: "all",
      photos: [],
      page: 1,
      itensPerPage: 5,
      pages: 0
    };

    this.handleRoverChange = this.handleRoverChange.bind(this);
    this.handleSolChange = this.handleSolChange.bind(this);
    this.handleCameraChange = this.handleCameraChange.bind(this);
  }

  componentDidMount() {
    this.loadPhotos();
  }

  loadPhotos = async () => {
    const { rover, sol } = this.state;
    fetch(`api/${rover}/photos?sol=${sol}`).then(response => {
      const pages = Math.ceil(
        response.data.photos.length / this.state.itensPerPage
      );
      this.setState({ photos: response.data.photos, pages });
    });
  };

  prevPage = () => {
    const { page } = this.state;

    if (page === 1) return;

    const newPage = page - 1;

    this.setState({ page: newPage });
  };

  nextPage = () => {
    const { page, pages } = this.state;

    if (page === pages) return;

    const newPage = page + 1;

    this.setState({ page: newPage });
  };

  handleRoverChange = changeRover => {
    this.setState(
      {
        rover: changeRover
      },
      () => {
        this.loadPhotos();
      }
    );
  };

  handleSolChange = sol => {
    this.setState(
      {
        sol: sol
      },
      () => {
        this.loadPhotos();
      }
    );
  };

  handleCameraChange = camera => {
    this.setState({
      camera: camera
    });
  };

  renderItens = (from, to, camera) => {
    const { photos, page, pages } = this.state;
    return (
      <div className="photo-list">
        <h1>
          <strong>{this.state.rover.toUpperCase()}</strong>
        </h1>
        {photos.slice(from, to).map(photo => (
          <article key={photo.id}>
            <strong>Photo #{photo.id}</strong>
            <p>{photo.earth_date}</p>
            <img src={photo.img_src} alt={photo.camera.full_name} />

            <Link
              to={{
                pathname: `/photos/${photo.id}`,
                state: { photo: photo }
              }}
            >
              +info
            </Link>
          </article>
        ))}
        <div className="actions">
          <button disabled={page === 1} onClick={this.prevPage}>
            Previous
          </button>
          <button disabled={page === pages} onClick={this.nextPage}>
            Next
          </button>
        </div>
      </div>
    );
  };

  render() {
    const { page, itensPerPage, rover, sol, camera } = this.state;

    return (
      <div>
        <SearchBar
          rover={rover}
          onRoverChange={this.handleRoverChange}
          sol={sol}
          onSolChange={this.handleSolChange}
          camera={camera}
          onCameraChange={this.handleCameraChange}
        />
        {this.renderItens(
          (page - 1) * itensPerPage,
          page * itensPerPage,
          camera
        )}
      </div>
    );
  }
}
